  }
body {
  background-image:url(/meet/static/media/city.d1887528.jpg);
  background-attachment: fixed;
  background-position: center;
}
.App {
  text-align: center;
  padding: 2% 5%;
}
.App * {
  box-sizing: border-box;
}
ul {
  padding: 0;
  list-style: none;
}
.EventItem {
  margin-bottom:25px;
  text-align:left;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}
.ToggleButtonWrapper {
  display: block;
  width: 100%;
  text-align: right;
}
.ToggleButton, .btn, .btn-link, :focus {
  outline: none !important;
  box-shadow: none !important;
}
p {
  margin: 5px 0;
}
.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}
input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  height: 38px;
}

.errorText {
  height:30px;
}

.errorText p {
  margin-bottom:0px;
}

.CitySearchWrapper {
  text-align: left;
}
.city {
  width: 100%;
  max-width:800px;
  margin-bottom:15px;
}
.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 600px;
  max-width: 600px;
  position: relative;
}
.CitySearch p {
  color:#fff;
}
.suggestions {
  width: 100%;
  max-width: 800px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 78px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}
.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}
.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.suggestions > li:hover {
  background-color: #bcdcdb;
}
.NumberInputWrapper {
  text-align:right;
}
.NumberInputWrapper p {
  color:#fff;
}
.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}
.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}
.name {
  font-size: 18px;
  font-weight: 600;
}
.group-name {
  text-transform: uppercase;
  font-size: 14px;
}
.going,
.visibility {
  font-style: italic;
}
.description {
  overflow-x: hidden;
}
.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}
.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.data-vis-wrapper {
